<script>
import BetterScroll from 'better-scroll'

export default {
  name: "Home",
  // 应该在mounted( )钩子内部创建better-scroll实例，
  // 因为created( )内部DOM元素尚未完成挂载，此时'.wrapper'根本获取不到任何DOM元素

  //最先能够拿到data和methods的是在created这个生命周期，
  // 最先能够操作真实dom的是在mounted这个生命周期
  created() {
    // created操作dom，必须包装this.$nextTick( ()=>{...} )
    //可以等待dom生成以后再来获取dom对象
    this.$nextTick(() => {
      console.log(this.$refs.aaa)
     const bScroll =  new BetterScroll('.wrapper', {
        useTransition: false,
        // 是否要实时侦测 0 1不侦测
        probeType: 3,
       pullUpLoad:true
      })
      bScroll.on('scroll',position=>{
        //console.log(position);
      })
      bScroll.on('pullingUp',()=>{
        console.log('pullingUp');
        setTimeout(()=>{
          bScroll.finishPullUp();
        },2000)
      })
    })
  }
}
</script>

<style scoped>
.wrapper {
  /*使用默认的方式实现滚动，但是在移动端效果不是很好，卡顿，这里使用better-scroll插件*/
  height: 500px;
  /*overflow-y: scroll;*/
  background-color: #ff8198;
}
